.profile-header {
	background-color: royalblue;
	font-family: serif;
	height: 70px;
	width: 100%;
	position: fixed;
	top: 0;
	z-index: 10;
	line-height: 70px;
}

.back-link {
	padding: 0 25px;
	background-color: rgb(202, 250, 114);
	font-weight: bold;
	float: left;
}

.login {
	float: right;
	background-color: rgb(202, 250, 114);
	font-weight: bold;
	padding: 0 25px;
	width: auto;
}

.All body {
	padding: 100px 0;
	background-image: url(https://beiz.jp/images_M/light/light_00021.jpg);
	background-size: cover;
	text-align: center;
}

.profile-title {
	font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

.profile-title h1 {
	font-size: 80px;
	color: black;
	opacity: 0.7;
	line-height: 30px;
}

.under {
	border-bottom: dotted 4px aliceblue;
}

.profile-contents {
	display: inline-block;
	text-align: center;
	margin-top: 50px;
	font-family: serif;
}

.profile-contents p {
	text-align: left;
	font-size: 20px;
	margin-bottom: 0px;
}

.fa-cat {
	padding-right: 10px;
}

.profile-contents table {
	border-collapse: collapse;
	width: 1000px;
	height: 100px;
	table-layout: fixed;
	border: 1px solid #fff;
	font-size: 20px;
}

.bubbles {
	position: relative;
	width: 100%;
	height: 100%;
	z-index: 0;
	top: 0;
	bottom: 0;
}

.bubbles {
	position: relative;
	width: 100%;
	height: 100%;
	z-index: 0;
	top: 0;
	bottom: 0;
}

.bubble {
	position: relative;
	width: 40px;
	height: 40px;
	background-color: transparent;
	border: 1px solid #fff;
	border-radius: 50%;
	animation: bubble 20s ease-in infinite;
}

.bubble:nth-child(1) {
	width: 20px;
	height: 20px;
	left: 10%;
	animation-duration: 6s;
}

.bubble:nth-child(2) {
	width: 30px;
	height: 30px;
	left: 20%;
	animation-delay: 5s;
}

.bubble:nth-child(3) {
	width: 30px;
	height: 30px;
	left: 30%;
	animation-duration: 5s;
}

.bubble:nth-child(4) {
	width: 50px;
	height: 50px;
	left: 45%;
	animation-duration: 8s;
	animation-delay: 3s;
}

.bubble:nth-child(5) {
	width: 10px;
	height: 10px;
	left: 60%;
	animation-duration: 11s;
}

.bubble:nth-child(6) {
	width: 30px;
	height: 30px;
	left: 70%;
	animation-duration: 5s;
	animation-delay: 6s;
}

.bubble:nth-child(7) {
	width: 25px;
	height: 25px;
	left: 80%;
	animation-duration: 6s;
}

.bubble:nth-child(8) {
	width: 55px;
	height: 55px;
	left: 5%;
	animation-duration: 10s;
}

@keyframes bubble {
	0% {
		bottom: 0px;
		transform: translateX(0);
	}
	25% {
		transform: translateX(50px);
	}
	50% {
		transform: translateX(100px);
	}
	75% {
		transform: translateX(30px);
	}
	100% {
		bottom: 2500px;
	}
}

@media screen and (max-width:1087px) {
	.profile-title h1 {
		font-size: 80px;
	}
}

@media screen and (max-width:480px) {
	.All body {
		padding: 100px 0;
		background-image: url(https://beiz.jp/images_M/light/light_00021.jpg);
		background-size: cover;
		text-align: center;
	}
	.profile-header {
		height: 50px;
		line-height: 40px;
		font-size: 10px;
	}
	.back-link {
		height: 50px;
		line-height: 50px;
	}
	.login {
		float: right;
		background-color: rgb(202, 250, 114);
		height: 50px;
		font-weight: bold;
		line-height: 50px;
	}
	.profile-title h1 {
		font-size: 30px;
		color: aliceblue;
	}
	.profile-title p {
		font-size: 20px;
		color: aliceblue;
	}
	.profile-contents table {
		border-collapse: collapse;
		width: 100%;
		height: 30px;
		table-layout: fixed;
		border: 1px solid #fff;
		font-size: 10px;
		padding: 0 10px;
	}
	.profile-contents p {
		font-size: 15px;
	}
	footer {
		font-size: 10px;
	}
}

.profile-contents th {
	background-color: #4091a0;
	color: white;
	font-weight: normal;
}

.profile-contents td {
	background-color: #78bbc7;
	padding: 20px;
}

footer {
	font-family: fantasy;
	margin: 30px 0px;
}